const hideLoadingFallback = () => {
  const loading = document.getElementById('loading-fallback')
  if (loading) {
    loading.style.display = 'none'
  }
}

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <ConfigProvider locale={zhCN}>
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </ConfigProvider>
  </React.StrictMode>,
)

